  scrollToTop = () => {
    this.setState({ isAnimating: true });
    let app = this;
    let lastTime = null;
    // console.log("scrolling up...");
    step();

    // timestamp: the number of milliseconds since Midnight Jan 1, 1970
    function step(timestamp) {
      let elapsed = 0;
      if (lastTime !== null) {
        elapsed = timestamp - lastTime;
      }
      let normal = 1000 / 60;
      let scale = 1;
      if (elapsed > 0) {
        scale = elapsed / normal;
      }
      // console.log("elapsed:", elapsed);
      // console.log("scale:", scale);

      // Scaling 1.1 with the time elapsed since last frame
      let scrollTop = window.scrollY;
      scrollTop /= 1.1 * scale;
      window.scroll(0, scrollTop);

      // If we haven't reached the top yet...
      if (window.scrollY > 1) {
        // requestAnimationFrame calls step after some delay (17ms~)
        lastTime = timestamp;

        if (app.isMouseDown == false) {
          requestAnimationFrame(step);
        }
      } else {
        window.scroll(0, 0);
        app.setState({ isAnimating: false });
      }
    }
  };

